import { Component, OnInit } from '@angular/core';
import { ValidateService} from '../../services/validate.service';
import { FlashMessagesService} from 'angular2-flash-messages';
import { AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['../../../assets/hlink/style.css',
  '../../../assets/hlink/font-awesome/css/font-awesome.min.css',
  '../../../assets/hlink/bootstrap/css/bootstrap.min.css']
})
export class RegisterComponent implements OnInit {
  name:String;
  username:String;
  email:String;
  password:String;
  usertype:String;
  userrole:String;

  constructor(
    private  validateService:ValidateService,
    private flashMessage:FlashMessagesService,
    private  authService:AuthService,
    private  router:Router
  ) { }

  ngOnInit() {
  }
  onRegisterSubmit(){
    const user={
      name: this.name,
      email:this.email,
      username: this.username,
      password: this.password,
      usertype:"1",
      userrole:"1"

    }
    // required fields
    if(!this.validateService.validateRegister(user)){
      this.flashMessage.show('Please Fill all fields',{cssClass: 'alert-danger', timeout:3000});
      //console.log('please fill in all fields');
      return false;
    }
    // required email fields
    if(!this.validateService.validateEmail(user.email)){
      //console.log('please use a  email');
      this.flashMessage.show('Please use a Valid email',{cssClass: 'alert-danger', timeout:3000});
      return false;
    }

    // register user to back end
    this.authService.registerUser(user).subscribe(data =>{
      if (data.success){
        this.flashMessage.show('You are registered, You can login',{cssClass: 'alert-success', timeout:3000});
        this.router.navigate(['/login']);

      }else {
        this.flashMessage.show('Some thing went wrong, Check again',{cssClass: 'alert-danger', timeout:3000});
        this.router.navigate(['/register']);
      }
    });
  }

}



